 const getdata = state => state.goods;
 const getgoods = state => state.goodsInfo;
 const gettask = state => state.taskTotle;
 const getdate = state => state.date;

 const getall = state => state;
 

export default{
    getdata,
    getgoods,
    getall,
    gettask,
    getdate
  }


